.infoBar {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    background: rgb(22, 192, 214);
    border-radius: 4px 4px 0 0;
    height: 60px;
    width: 100%;
  }
  
  .leftInnerContainer {
    -webkit-flex: 0.5 1;
            flex: 0.5 1;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    margin-left: 5%;
    color: white;
  }
  
  .rightInnerContainer {
    display: -webkit-flex;
    display: flex;
    -webkit-flex: 0.5 1;
            flex: 0.5 1;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    margin-right: 5%;
  }
  
  .onlineIcon {
    margin-right: 5%;
  }
.form {
    display: -webkit-flex;
    display: flex;
    border-top: 2px solid #D3D3D3;
  }
  
  .input {
    border: none;
    border-radius: 0;
    padding:2%;
    width: 80%;
    font-size: 1.2em;

  }
  
  input:focus, textarea:focus, select:focus{
    outline: none;
  }
  
  .sendButton {
    color: #fff;
    font-size: 25px;
    text-decoration: none;
    background: rgb(22, 192, 214);
    padding: 15px;
    display: inline-block;
    border: none;
    width: 25%;
  }
.messageBox {
    background: #F3F3F3;
    
    border-radius: 20px 20px 20px 5px;
    padding: 5px 20px;
    color: white;
    display: inline-block;
    max-width: 80%;
  }
  
  .messageText {
    width: 100%;
    letter-spacing: 0;
    float: left;
    font-size: 1.1em;
    word-wrap: break-word;
  }
  
  .messageText img {
    vertical-align: middle;
  }
  
  .messageContainer {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    padding: 0 5%;
    margin-top: 3px;
  }
  
  .sentText {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    font-family: Helvetica;
    color: black;
    letter-spacing: 0.3px;
  }
  
  .pl-10 {
    padding-left: 10px;
  }
  
  .pr-10 {
    padding-right: 10px;
  }
  
  .justifyStart {
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
  }
  
  .justifyEnd {
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
  }
  
  .colorWhite {
    color: black;
  }
  
  .colorDark {
    color: #353535;
  }
  
  .backgroundBlue {
    background: #bebcbc;
    border-radius: 20px 20px 5px 20px;
  }
  
  .backgroundLight {
    background: #F3F3F3;
  }
.messages {
    padding: 5% 0;
    overflow: auto;
    -webkit-flex: auto;
            flex: auto;
    margin: 5px yellow;
     background-color: rgb(230, 215, 189);
}
