.form {
    display: flex;
    border-top: 2px solid #D3D3D3;
  }
  
  .input {
    border: none;
    border-radius: 0;
    padding:2%;
    width: 80%;
    font-size: 1.2em;

  }
  
  input:focus, textarea:focus, select:focus{
    outline: none;
  }
  
  .sendButton {
    color: #fff;
    font-size: 25px;
    text-decoration: none;
    background: rgb(22, 192, 214);
    padding: 15px;
    display: inline-block;
    border: none;
    width: 25%;
  }